import { Component, OnInit } from '@angular/core';
import { item } from '../item';
import { DataService } from '../data.service';

@Component({
  selector: 'app-shopping-item',
  templateUrl: './shopping-item.component.html',
  styleUrls: ['./shopping-item.component.css']
})
export class ShoppingItemComponent implements OnInit {
  shoppingItemsList: item[] = [];

  constructor(private ds: DataService) { }
  getItems() {
    this.ds.getShoppingItems().subscribe(items => {
      this.shoppingItemsList = items;
      console.log(this.shoppingItemsList);
    });
  }
  addItem() {
    let newItem = {
      "itemName": "lollipops",
      "numberOfItems": "4",
      "bought": "true"
    }
    this.ds.addShoppingItem(newItem).subscribe(item => {
      console.log(item);
      this.getItems();
    })
  }
  deleteItem(id) {
    debugger;
    this.ds.deleteShoppingItem(id).subscribe(data => {
      if (data.n === 1) {
        for (let i = 0; i < this.shoppingItemsList.length; i++) {
          if (id === this.shoppingItemsList[i]._id) {
            this.shoppingItemsList.splice(i, 1);
            this.getItems();
          }
        }
      }
    })
  }

  ngOnInit() {
    this.getItems();
  }

}
